body {
    font: 48px "Century Gothic", Futura, sans-serif;
    font-weight: bold;
    margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  border: 1px solid #999;
  float: center;
  font-size: 72px;
  font-weight: bold;
  margin-right: 0;
  margin-top: 0;
  padding: 0;
  text-align: center;
  width: 100%;
  height: 100%;
  touch-action: manipulation;
}

.square:focus {
  outline: none;
}
  

